import React from "react";
import { Form, Switch } from "antd";
const CSwitch = ({
  name,
  label,
  isRequired,
  validate,
  onChange,
  value,
  ...rest
}) => {
  return (
    <Form.Item
      label={
        <span>
          {label}
          {isRequired ? (
            <span style={{ marginLeft: "5px", color: "red" }}>*</span>
          ) : null}
        </span>
      }
      name={name}
      {...rest}
      validateStatus={validate ? "error" : null}
      help={validate}
    >
      <Switch onChange={onChange} checked={value} />
    </Form.Item>
  );
};

export default CSwitch;
